.diagramContainer {
	position: absolute;
	top: 58px;
	bottom: 0;
	left: 0;
	right: 0;
	overflow: hidden;
}

.tooltip {
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1111;
	color: #222;
	background: #fff;
	border-radius: 4px;
	border: 1px solid #9dbfed;
	font-size: 13px;
	padding: 8px 10px;
	transition: opacity 0.2s ease-out;
	box-sizing: border-box;
	line-height: 17px;
	opacity: 0;
	visibility: hidden;
	text-align: center;

	p {
		margin: 0;
	}

	&:before,
	&:after {
		top: 100%;
		left: 50%;
		border: solid transparent;
		content: '';
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	&:after {
		border-color: rgba(255, 255, 255, 0);
		border-top-color: #fff;
		border-width: 6px;
		margin-left: -6px;
	}

	&:before {
		border-color: rgba(194, 225, 245, 0);
		border-top-color: #9dbfed;
		border-width: 7px;
		margin-left: -7px;
	}

	&:global(.bottom):after,
	&:global(.bottom):before {
		bottom: 100%;
		top: auto;
		left: 50%;
		border: solid transparent;
		content: '';
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	&:global(.bottom):after {
		border-color: rgba(255, 255, 255, 0);
		border-bottom-color: #fff;
		border-width: 6px;
		margin-left: -6px;
	}

	&:global(.bottom):before {
		border-color: rgba(194, 225, 245, 0);
		border-bottom-color: #9dbfed;
		border-width: 7px;
		margin-left: -7px;
	}

	&:global(right):after,
	&:global(right):before {
		left: auto;
		right: 100%;
		top: 50%;
		border: solid transparent;
		content: '';
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	&:global(right):after {
		border-color: rgba(255, 255, 255, 0);
		border-right-color: #fff;
		margin-left: auto;
		border-width: 6px;
		margin-top: -6px;
	}

	&:global(right):before {
		border-color: rgba(157, 191, 237, 0);
		border-right-color: #9dbfed;
		margin-left: auto;
		border-width: 7px;
		margin-top: -7px;
	}

	&:global(left):after,
	&:global(left):before {
		left: 100%;
		top: 50%;
		border: solid transparent;
		content: '';
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	&:global(left):after {
		border-color: rgba(255, 255, 255, 0);
		border-left-color: #fff;
		margin-left: auto;
		border-width: 6px;
		margin-top: -6px;
	}

	&:global(left):before {
		border-color: rgba(157, 191, 237, 0);
		border-left-color: #9dbfed;
		margin-left: auto;
		border-width: 7px;
		margin-top: -7px;
	}

	&:global(dark) {
		color: #fff;
		background: #222;
		border: 1px solid #6d819c;
	}

	&:global(dark):after {
		border-color: rgba(255, 255, 255, 0);
		border-top-color: #222;
	}

	&:global(dark):before {
		border-color: rgba(194, 225, 245, 0);
		border-top-color: #6d819c;
	}

	&:global(dark):global(.bottom):after {
		border-color: rgba(255, 255, 255, 0);
		border-bottom-color: #222;
	}

	&:global(dark):global(.bottom):before {
		border-color: rgba(194, 225, 245, 0);
		border-bottom-color: #6d819c;
	}

	&:global(dark):global(right):after {
		border-color: rgba(255, 255, 255, 0);
		border-right-color: #222;
	}

	&:global(dark):global(right):before {
		border-color: rgba(194, 225, 245, 0);
		border-right-color: #6d819c;
	}

	&:global(dark):global(left):after {
		border-color: rgba(255, 255, 255, 0);
		border-left-color: #222;
	}

	&:global(dark):global(left):before {
		border-color: rgba(194, 225, 245, 0);
		border-left-color: #6d819c;
	}
}
