.info-card {
	background: #2b3250;
	position: fixed;
	width: 320px;
	height: calc(100% - 58px);
	left: -320px;
	opacity: 0;
	bottom: 0;
	color: #fff;
	z-index: 101;
	box-shadow: 0px 0px 40px 16px rgba(11, 16, 40, 0.94);
	overflow: auto;
	transition: all 200ms;
	display: flex;
	flex-direction: column;
}

.info-card.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.info-card__show {
	position: absolute;
	color: #859de0;
	font-size: 12px;
	transform: rotate(-90deg);
	right: -5px;
	top: 76px;
	cursor: pointer;
	display: none;
}

.info-card.open {
	left: 0;
	opacity: 1;
}

.info-card.open.collapsed {
	left: -267px;
}

.info-card.open.collapsed .info-card__show {
	display: block;
}

.info-card.open.collapsed .info-card__close {
	transform: rotate(180deg);
}

.info-card__header {
	background: #29367a;
	padding: 24px;
}

.info-card.light .info-card__header {
	color: #fff;
}

.info-card__subtitle {
	font-size: 14px;
	color: #859de0;
	font-weight: 500;
}

.info-card__title {
	font-size: 14px;
	text-transform: uppercase;
	line-height: 25px;
	font-weight: 500;
	margin-top: 8px;
}

.info-card__section {
	padding: 18px;
	border-color: transparent;
	border-width: 6px;
	border-style: solid;
}

.info-card__section-title {
	color: #5872da;
	font-size: 20px;
	font-weight: 500;
	line-height: 28px;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
}

.info-card__item {
	margin-top: 16px;
	font-size: 14px;
	line-height: 18px;
}

.info-card__item-title {
	color: #859de0;
}

.info-card__item-body.red {
	color: #EB5757;
}

.info-card__item-body p {
	margin-top: 6px;
}

.info-card__info {
	background: #5872da;
	padding: 16px 24px 8px;
	font-size: 14px;
}

.info-card.light .info-card__info {
	color: #fff;
}

.info-card__info-item {
	margin-bottom: 8px;
}

.info-card__info-item > span {
	opacity: 0.6;
	margin-right: 4px;
}

.info-card__button {
	margin: 24px 0;
}

.info-card__close {
	position: absolute;
	top: 20px;
	right: 19px;
	cursor: pointer;
	fill: #5872da;
	z-index: 111;
}

.info-card__body > div {
	margin-bottom: -16px !important;
}
