.zoom {
	position: fixed;
	width: 320px;
	/* height: 56px; */
	z-index: 4;
	bottom: 8px;
	right: 8px;
	background: #fff;
	/* display: flex; */
	padding: 8px;
	border-radius: 8px;
	box-sizing: border-box;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.zoom.dark {
	background: #2b3250;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px, rgba(0, 0, 0, 0.2) 0px 15px 40px;
}

.zoom__minus,
.zoom__plus,
.zoom__mapicon {
	width: 40px;
	height: 40px;
	border-radius: 8px;
	cursor: pointer;
}

.zoom__minus:hover,
.zoom__plus:hover,
.zoom__mapicon:hover {
	background: rgba(255, 255, 255, 0.1);
}

.zoom.light .zoom__minus:hover,
.zoom.light .zoom__plus:hover,
.zoom.light .zoom__mapicon:hover {
	background: rgba(41, 44, 67, 0.1);
}

.input-range__track-container {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
}

.input-range__track {
	width: 100%;
	height: 3px;
	border-radius: 3px;
}

.input-range__thumb {
	width: 11px;
	height: 11px;
	background-color: #859de0;
	border-radius: 100%;
}
