@import '../../../../styles/styles';

.list {
	position: relative;
	user-select: none;
	&__table {
		overflow-y: auto;
		max-height: calc(100vh - 287px);
	}
	thead {
		position: sticky;
		top: 0;
		z-index: 10;
		background: #fff;
	}
	th {
		font-size: 10px;
		line-height: 12px;
		border-bottom: 0.5px solid #4f4f4f;
		padding: 7px 8px;
		&:first-child {
			padding: 7px 8px 7px 8px;
		}
		&:last-child {
			padding: 7px 0 7px 8px;
		}
		&:nth-child(7),
		&:nth-child(8),
		&:nth-child(9) {
			text-align: right;
		}
	}
	td {
		padding: 9px 8px;
		font-size: 12px;
		line-height: 18px;
		border-bottom: 0.5px solid #f0f0f0;
		&:first-child {
			padding: 9px 8px 9px 8px;
		}
		&:last-child {
			padding: 9px 0 9px 8px;
		}
		&:nth-child(7),
		&:nth-child(8),
		&:nth-child(9) {
			text-align: right;
		}
		& > span {
			display: flex;
			align-items: center;
		}
	}
	tr {
		&:last-child {
			td {
				border-bottom: none;
			}
		}
	}
}

.total {
	font-size: 11px;
	margin-top: 12px;
	padding-left: 8px;
}

.selectedCount {
	margin-left: 16px;
}

.nodata {
	padding: 0;
}

.filters {
	&__wrapper {
		position: relative;
	}

	&__title {
		cursor: pointer;
	}

	&__icon {
		margin-left: 3px;
		position: relative;
		top: 1px;
	}

	&__label {
		margin-bottom: 4px;
		font-size: 9px;
	}

	&__fields {
		white-space: nowrap;
		min-width: 210px;
		:global(.registry__input) {
			display: inline-block;
		}
		:global(.registry__select) {
			font-size: 10px;
		}
		:global(.registry__input > div:nth-child(1)) {
			margin-bottom: 0;
		}
		:global(.registry__select > div:nth-child(1)) {
			margin-bottom: 0;
		}
		:global(.registry__select > div:nth-child(2)) {
			height: 24px;
			padding: 3px 3px 0;
		}
		:global(.registry__select ._3x1chKtkGYrYL4rf4hsTl3) {
			padding: 3px 9px;
		}
		:global(.registry__select > div:nth-child(2) ._3YoCWaoOUiOEr786cAzZKV) {
			max-width: 70%;
		}
	}

	&__container {
		position: absolute;
		background: #ffffff;
		box-shadow: 0px 16px 37px rgba(0, 0, 0, 0.22);
		padding: 12px;
		z-index: 111;
		right: 0px;
		top: 100%;
		margin-top: 4px;
		text-align: left;

		:global(.registry__input input) {
			height: 24px;
			min-width: 160px;
			font-size: 10px;
			padding: 0 8px;
			padding-right: 24px;
		}

		:global(.registry__select .registry__select__control) {
			min-height: 24px;
			max-height: 24px;
		}

		:global(.registry__select .registry__select__value-container) {
			padding: 0px 5px;
		}

		:global(.registry__select .registry__select__dropdown-indicator) {
			padding: 0 5px;
		}

		:global(.registry__select .registry__select__option) {
			padding: 2px 8px;
		}

		:global(.registry__select .registry__select__single-value > img) {
			width: 14px;
			height: 14px;
			margin-right: 4px;
		}

		&:after {
			bottom: 100%;
			right: 0px;
			border: solid transparent;
			content: '';
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none;
			border-color: rgba(255, 255, 255, 0);
			border-bottom-color: #fff;
			border-width: 6px;
		}

		&.left {
			right: auto;
			left: 100%;
			margin-left: -11px;
			&:after {
				right: auto;
				left: 0;
			}
		}
	}
}

.integral {
	display: inline-block;
	width: 12px;
	min-width: 12px;
	height: 12px;
	border: 1px solid $light_blue;
	border-radius: 50%;
	margin-right: 4px;
	position: relative;
	top: -1px;
}

.fullwidth {
	:global(.registry__input) {
		width: 100%;
	}
}
