.side-menu {
	background: #fff;
	color: #2b3250;
	position: absolute;
	left: 8px;
	top: 8px;
	padding: 8px;
	width: 56px;
	border-radius: 8px;
	z-index: 100;
	overflow: hidden;
	box-sizing: border-box;
	max-height: 568px;
	height: calc(100vh - 56px);
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.side-menu.dark {
	background: #2b3250;
	color: #fff;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px, rgba(0, 0, 0, 0.2) 0px 15px 40px;
}

.side-menu__item {
	height: 40px;
	text-align: center;
	line-height: 40px;
	font-weight: 500;
	cursor: pointer;
	border-radius: 6px;
	font-size: 18px;
	margin-bottom: 6px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: nowrap;
}

.side-menu__item:nth-child(9) .side-menu__image,
.side-menu__item:nth-child(10) .side-menu__image {
	padding-left: 4px;
}

.side-menu__image {
	display: flex;
	width: 40px;
	min-width: 40px;
	height: 40px;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
}

.side-menu__item img {
	opacity: 0.4;
	max-width: 30px;
}

.side-menu__item.active img {
	opacity: 1;
}

.side-menu__item.disabled {
	cursor: auto;
}

.side-menu__item.disabled img {
	opacity: 0.15;
}

.side-menu__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.light .side-menu__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.side-menu__more {
	position: absolute;
	left: 8px;
	bottom: 3px;
	background: #2b3250;
}

.side-menu__more:hover {
	background: #424a6b;
}

.light .side-menu__more {
	background: #fff;
}

.light .side-menu__more:hover {
	background: #e9e9ec;
}

.side-menu__title {
	display: none;
	font-size: 14px;
	font-weight: 400;
	margin-left: 10px;
	margin-right: 16px;
}

.side-menu.open {
	height: 100%;
	max-height: 100% !important;
	padding: 16px 16px;
	left: 0;
	top: 0;
	border-radius: 0;
	width: 432px;
}

.side-menu.open .side-menu__title {
	display: inline-block;
	white-space: nowrap;
}

.side-menu.collapsed .side-menu__more {
	display: none;
}

.side-menu.open .__react_component_tooltip {
	display: none !important;
}

.side-menu__scrollbar {
	height: 100%;
}

.side-menu__scrollbar-track {
	pointer-events: none;
}

.side-menu__tooltip {
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1111;
	color: #222;
	background: #fff;
	border-radius: 4px;
	border: 1px solid #9dbfed;
	font-size: 13px;
	padding: 8px 10px;
	transition: opacity 0.2s ease-out;
	box-sizing: border-box;
	line-height: 18px;
	opacity: 0;
	visibility: hidden;
	text-align: center;
}

.side-menu__tooltip:after,
.side-menu__tooltip:before {
	left: auto;
	right: 100%;
	top: 50%;
	border: solid transparent;
	content: '';
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.side-menu__tooltip:after {
	border-color: rgba(255, 255, 255, 0);
	border-right-color: #fff;
	margin-left: auto;
	border-width: 6px;
	margin-top: -6px;
}
.side-menu__tooltip:before {
	border-color: rgba(157, 191, 237, 0);
	border-right-color: #9dbfed;
	margin-left: auto;
	border-width: 7px;
	margin-top: -7px;
}

.side-menu__tooltip.dark {
	color: #fff;
	background: #222;
	border: 1px solid #6d819c;
}

.side-menu__tooltip.dark:after {
	border-color: rgba(255, 255, 255, 0);
	border-right-color: #222;
}
.side-menu__tooltip.dark:before {
	border-color: rgba(194, 225, 245, 0);
	border-right-color: #6d819c;
}

.side-menu > div > div:last-child {
	width: 2px !important;
	border-radius: 0 !important;
	right: 0 !important;
}
