@import '../../../../../../../styles/styles';

.item {
	position: relative;
	color: $main_font_color;
	transform: scale(1);
	& > td {
		padding: 0;
		border: none;
		font-size: 11px;
		line-height: 16px;
		position: relative;
		z-index: 1;
		& > span {
			display: inline-block;
			width: 100%;
			box-sizing: border-box;
			padding: 10px 5px;
			margin-bottom: 6px;
			position: relative;
		}
		&.endSection {
			& > span {
				padding: 10px 15px 10px 5px;
			}
			&::after {
				background: $light_grey_color;
				content: '';
				width: 1px;
				height: 24px;
				position: absolute;
				right: 0;
				top: 50%;
				margin-top: -12px;
			}
			&.startSection {
				& > span {
					padding: 15px;
				}
			}
		}
		&.startSection {
			& > span {
				padding: 10px 5px 10px 15px;
			}
		}
		&:first-child {
			& > span {
				padding-left: 20px;
			}
		}
		&:last-child {
			&::after {
				right: 20px;
			}
			& > span {
				padding-right: 20px;
			}
		}
		&:nth-child(18) {
			min-width: 200px;
		}
		&.noticeSourceStr {
			min-width: 120px;
		}
		&.statusStr {
			min-width: 105px;
		}
	}
	&:after {
		content: '';
		background: #fff;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 6px;
		width: 100%;
		height: calc(100% - 6px);
		box-shadow: 0 0 10px rgba(9, 16, 28, 0.07);
		border-radius: 4px;
	}
	&__dots {
		min-width: 35px;
		& > span {
			padding-left: 11px !important;
			padding-right: 0 !important;
			line-height: 11px;
		}
		svg {
			padding: 0 4px;
			cursor: pointer;
		}
		&::after {
			background: $light_grey_color;
			content: '';
			width: 1px;
			height: 24px;
			position: absolute;
			right: 0;
			top: 50%;
			margin-top: -16px;
		}
	}
	&__flags {
		line-height: 0;
		text-align: center;
		display: flex !important;
		align-items: center;
		justify-content: center;
	}
	&__flag {
		width: 24px;
		height: 24px;
		object-fit: cover;
		border-radius: 50%;
		margin: 0 20px;
	}
	&__arrow {
		margin: 0 10px;
	}
	&__actual {
		text-align: center;
		line-height: 0;
	}
	&.disabled {
		filter: grayscale(1);
		color: $grey_color;
	}
}

.tooltip {
	display: inline-block;
	max-width: 350px;
	line-height: 16px;
}
