.header {
	background: #fff;
	color: #2b3250;
	height: 58px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	z-index: 102;
	font-size: 14px;
	padding: 0 12px;
	/* box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24); */
}

.header.dark {
	background: #2b3250;
	color: #fff;
	/* box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px, rgba(0, 0, 0, 0.2) 0px 15px 40px; */
}

.header__left,
.header__right {
	display: flex;
	align-items: center;
}

.header__item {
	margin: 0 4px;
	height: 40px;
	min-width: 19px;
	padding: 0 10px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	cursor: pointer;
}

.header__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.header.light .header__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.header__item img,
.header__item svg {
	opacity: 0.4;
}

.header__item.active img,
.header__item.active svg {
	opacity: 1;
}

.header__item input {
	background: transparent;
	color: inherit;
	font-family: 'Roboto';
	font-size: 14px;
	border: none;
	width: 23px;
}

.header__item-zoom:after {
	content: '%';
}
