@import '../../../../styles/styles';

.card {
	position: absolute;
	z-index: 1111;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #f5f9fb;
	&__list {
		padding: 24px 47px;
		@include respond-to('small') {
			padding: 24px 24px;
		}
	}
	&__close {
		cursor: pointer;
	}
	&__top {
		margin-bottom: 32px !important;
	}
}
