@import '../../../../styles/styles';

.section {
	padding: 18px;
	border-color: transparent;
	border-width: 6px;
	border-style: solid;
	min-height: calc(100% - 48px);
}

.rb {
	&__row {
		display: flex;
		margin: 0 -12px;
	}
	&__col {
		width: 50%;
		padding: 0 12px;
	}
	&__col_full {
		width: 100%;
		padding: 0 12px;
	}
	&__col_button {
		margin-top: 16px;
	}
}
