@import '../../styles/styles';

.buttons {
	display: flex;
	margin: 15px 0;
	align-items: center;
}

.extract {
	margin-right: 6px;
}

.clear {
	white-space: nowrap;
}

.filters {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-left: 3px;
}

.filter {
	display: flex;
	background: #f0f0f0;
	border-radius: 2px;
	font-size: 10px;
	line-height: 16px;
	height: 16px;
	margin: 3px;
	padding: 1px 6px;
	align-items: center;
	&__icon {
		line-height: 0;
		margin: 0 4px;
		img {
			max-width: 14px;
		}
	}
	&__clear {
		cursor: pointer;
		padding: 2px;
		line-height: 0;
		margin-left: 6px;
		& > svg {
			max-width: 4px;
		}
	}
}

.header {
	font-size: 12px;
	line-height: 14px;

	&__line {
		margin-bottom: 6px;
	}

	&__label {
		color: $grey_color;
	}
}

.inValue {
	color: #859de0;
}

.integral {
	display: inline-block;
	width: 10px;
	min-width: 10px;
	height: 10px;
	border: 1px solid $light_blue;
	border-radius: 50%;
}
