.map__close {
	position: absolute;
	right: 0;
	top: 0;
	cursor: pointer;
}

.zoom__map {
	z-index: 100;
	position: absolute;
	overflow: hidden;
	bottom: 64px;
	right: 8px;
	width: 280px;
	height: 145px;
	background: #2b3250;
	padding: 15px 15px 0 15px;
	border-radius: 8px 8px 0 0;
	box-sizing: border-box;
	visibility: hidden;
	opacity: 0;
}

.zoom__map.map__open {
	visibility: visible;
	opacity: 1;
}

.zoom__map.light {
	background: #fff;
	border-color: #fff;
}

.zoom__map.light .map__overview {
	background: #eef0f5;
}
