@import '../../../../../styles/styles';

.integral {
	border: 1px solid $light_blue;
	border-radius: 50%;
	height: 18px;
	width: 18px;
}

.item {
	user-select: none;
	&.selected {
		background: rgba(79, 153, 238, 0.1);
		&:hover {
			background: rgba(79, 153, 238, 0.1);
		}
	}
	&:hover {
		background: rgba(0, 108, 232, 0.03);
		cursor: pointer;
	}
}

.check {
	width: 14px;
	height: 14px;
	border: 1px solid $light_blue;
	box-sizing: border-box;
	border-radius: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
	&.selected {
		background: $light_blue;
	}
}

.inValue {
	color: #859de0;
}

.balance {
	img {
		max-width: 16px;
		margin-right: 5px;
	}
}

.risk {
	img {
		max-width: 18px;
		margin-right: 5px;
	}
}

.tech {
	text-align: center;
	line-height: 0 !important;
	&__icon {
		border: 1px solid;
		border-radius: 4px;
		width: 20px;
		height: 20px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		img {
			max-width: 100%;
		}
	}
}

.flags {
	display: inline-flex;
	align-items: center;
	justify-content: center;
}

.arrow {
	margin: 0 8px;
}

.flag {
	border-radius: 50%;
	width: 20px;
	height: 20px;
	object-fit: cover;
	&.country_112 {
		object-position: left;
	}
}

.tooltip {
	max-width: 600px;
	display: inline-block;
	white-space: normal;
	line-height: 16px;
	z-index: 1111111111111;
}
