@import '../../../../../../styles/styles';

.list {
	position: relative;
	&__table {
		overflow: auto;
		max-height: calc(100vh - 180px);
	}
	&__header {
		font-size: 9px;
		font-weight: 500;
		text-transform: uppercase;
		position: sticky;
		top: 0;
		background: #f5f9fb;
		z-index: 1;
		th {
			padding: 5px 0 11px;
			border-bottom: 1px solid rgba(88, 87, 87, 0.2);
			position: sticky;
			top: 0;
			background: #f5f9fb;
			z-index: 1;
			& > span {
				border-right: 1px solid $light_grey_color;
				display: inline-block;
				line-height: 16px;
				padding: 0 15px 0;
				width: 100%;
				box-sizing: border-box;
				text-align: center;
			}
			&:first-child {
				padding: 5px 0 11px 20px;
				& > span {
					padding: 0 15px 0 0;
					height: 14px;
				}
			}
			&:last-child {
				padding: 5px 20px 11px 0;
				& > span {
					padding: 0 0 0 15px;
				}
			}
		}
	}
	&__subheader {
		position: sticky;
		top: 33px;
		background: #f5f9fb;
		z-index: 2;
		th {
			box-sizing: border-box;
			padding: 6px 5px;
			position: relative;
			vertical-align: bottom;
			position: sticky;
			top: 33px;
			background: #f5f9fb;
			z-index: 2;
			&.endSection {
				padding: 6px 15px 6px 5px;
				&::after {
					background: $light_grey_color;
					content: '';
					width: 1px;
					height: 16px;
					position: absolute;
					right: 0;
					top: 50%;
					margin-top: -8px;
				}
			}
			&.startSection {
				padding: 6px 5px 6px 15px;
			}
			&:first-child {
				padding: 6px 5px 6px 20px;
			}
			&:last-child {
				padding: 6px 35px 6px 5px;
				&::after {
					right: 20px;
				}
			}
		}
	}
}
